nav {
    display: flex;
    top: 0;
    left: 0;
    width: 100%;
    position: sticky;
    z-index: 1000;
    justify-content: space-around;
    align-items: center;
    min-height: 7vh;
    background-color: $primary;
    font-family: 'Roboto', sans-serif;
    height: 70px;
    box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;

    @media screen and (max-width:786px) {
        justify-content: space-between;
        padding: 0 1rem;
    }

    .logo {
        color: rgb(226, 226, 226);
        text-transform: uppercase;
        font-size: 20px;
        font-family: 'Roboto', sans-serif;
        display: flex;
        align-items: center;

        h4 {
            margin: 0 1rem;
        }
    }

    .nav-imgs {
        width: 30px;
        height: 30px;
        margin: 0 12px;
        display: inline-flex;
        background: #21212b;
        vertical-align: middle;
        border-radius: 50%;
    }

    .iste-logo {
        height: 55px;
        float: left;
        border-radius: 50%;
    }

    .burger {
        display: none;
        background-color: $primary;
        margin-right: 15px;

        div {
            width: 25px;
            height: 3px;
            background-color: rgb(226, 226, 226);
            margin: 4px;
            transition: all 0.3s ease;
        }
    }

    .nav-links {
        display: flex;
        justify-content: space-around;
        width: 30%;
        background-color: $primary;

        li {
            display: flex;
            list-style: none;
            background-color: $primary;
            align-items: center;
            padding: 0 1rem;

        }

        li a:before {
            content: '';
            transition: all 0.3s linear;
        }

        li a:hover:before {
            width: 80%;
        }

        li a:hover {
            /*border-bottom: 2px solid #fff;*/
            /*letter-spacing: .2rem;*/
            transition: transform 500ms ease-in-out 25ms;
            transform: scale(1.1);
        }

        a {
            color: rgb(226, 226, 226);
            text-decoration: none;
            letter-spacing: 1px;
            font-weight: bold;
            font-size: 1rem;
        }
    }

    @media screen and (max-width:1024px) {
        .nav-links {
            width: 50%;
        }
    }


    @media screen and (max-width:786px) {
        .nav-links {
            justify-content: flex-start;
            position: fixed;
            right: 0px;
            height: 95vh;
            top: 7vh;
            background-color: $secondary;
            display: flex;
            flex-direction: column;
            align-items: center;
            transform: translateX(100%);
            transition: transform 0.5s ease-in;
            z-index: 10;

            li {
                padding: 1rem;
                width: 100%;
                box-shadow: 0px 4px 4px rgba($color: $primary, $alpha: 0.7);
                margin: 0.1rem 0;
                transition: 0.1s ease-in-out all;

                &:hover {
                    background-color: rgba($color: $primary, $alpha: 0.5);
                }
            }
        }

        .nav-links li {
            opacity: 0;
        }

        .burger {
            display: block;
            cursor: pointer;
        }

    }

    @media only screen and (max-width: 576px) {
        .nav-title {
            display: none;
        }

    }

    .nav-active {
        transform: translateX(0%);
    }

    @keyframes navLinkFade {
        from {
            opacity: 0;
            transform: translateX(50px);
        }

        to {
            opacity: 1;
            transform: translateX(0px);
        }
    }

    .toggle .line1 {
        transform: rotate(-45deg) translate(-5px, 5px);
    }

    .toggle .line2 {
        opacity: 0;
    }

    .toggle .line3 {
        transform: rotate(45deg) translate(-5px, -5px);
    }

}