.home {

    .cards {
        display: flex;
        flex-wrap: wrap;


        @media screen and (max-width:1380px) {
            justify-content: center;
        }

    }

    .domain-card {
        background-color: #243b55;
        padding: 1rem;
        border-radius: 1rem;
        width: 45%;
        min-width: 30rem;
        // height: 20rem;
        margin: 2rem;
        /*border: 2px solid white;*/
        box-shadow: 0px 4px 20px rgba($color: $primary, $alpha: 0.7);
        // text-align: center;

        // box-shadow: rgba(136, 165, 191, 0.48) 6px 2px 16px 0px, rgba(255, 255, 255, 0.8) -6px -2px 16px 0px;
        h1 {
            font-size: 2rem;
            font-weight: 500;
        }

        img {
            margin-bottom: 1rem;
            width: 8rem;
        }

        p {
            opacity: .8;
            font-size: 1.2rem;
            margin-top: 1rem;

        }

        a {
            width: 100%;
            display: inline-block;
            margin-top: 1.5rem;
            padding: .5rem;
            border-radius: 10px;
            background-color: $primary;
            color: $text-color;
            text-align: center;
            border: 1px solid #221132;

        }

        a:hover {
            background: #FFFFFF;
            color: #232B38;
            border: 1px solid #232B38;
            transition: all 0.3s ease;
            -webkit-transition: all 0.3s ease;
            -moz-transition: all 0.3s ease;
            -ms-transition: all 0.3s ease;
            -o-transition: all 0.3s ease;
        }
    }
}