.tasks {
	padding: 2rem;

	h1 {
		font-size: 2.5rem;
		margin-bottom: 3rem !important;
	}

	.cards {
		display: flex;
		flex-direction: column;
		align-items: center;
	}

	.task-card {
		/*border: 2px solid white;*/

		position: relative;
		background-color: $secondary;
		padding: 2rem;
		border-radius: 25px;
		width: 70%;
		margin: 1rem;
		justify-self: center;
		box-shadow: rgba(50, 50, 93, 0.25) 0px 13px 27px -5px,
			rgba(0, 0, 0, 0.3) 0px 8px 16px -8px;

		@media only screen and (max-width: 576px) {
			width: auto;
			margin: 1rem 2rem;
		}

		p {
			opacity: 0.8;
			margin-top: 1rem;
		}

		a {
			width: 100%;
			display: inline-block;
			margin-top: 1.5rem;
			padding: 0.5rem;
			border-radius: 10px;
			background-color: $primary;
			color: $text-color;
			text-align: center;
			border: 1px solid #221232;
		}

		a:hover {
			background: #ffffff;
			color: #232b38;
			border: 1px solid #232b38;
			transition: all 0.3s ease;
			-webkit-transition: all 0.3s ease;
			-moz-transition: all 0.3s ease;
			-ms-transition: all 0.3s ease;
			-o-transition: all 0.3s ease;
		}
	}

	.bottom {
		display: flex;
		justify-content: space-between;

		&:hover .img {
			text-decoration: underline;
		}

		.img {
			display: flex;
			flex-direction: column;
			align-items: center;
			font-size: 0.7rem;
			transition: 0.2s all;
		}

		p {
			width: 60%;
		}

		img {
			width: 3.5rem;
			margin-bottom: 0.2rem;
		}
	}

	.completed {
		font-size: 1.5rem;
		color: $success;
		position: absolute;
		top: 0;
		display: flex;
		justify-content: center;
		align-items: center;
		right: 0;
		transform: translate(25%, -25%);
		background-color: #fff;
		border-radius: 100%;
		width: 20px;
		height: 20px;
		transform: scale(1.5);
	}

	.submitted {
		font-size: 1.5rem;
		color: $warning;
		position: absolute;
		top: 0;
		display: flex;
		justify-content: center;
		align-items: center;
		right: 0;
		transform: translate(25%, -25%);
		background-color: #fff;
		border-radius: 100%;
		width: 20px;
		height: 20px;
		transform: scale(1.5);
	}

	.date {
		opacity: 0.6;
	}
}

.completed-alert {
	background-color: $success;
	padding: 1rem;
	width: 70%;
	margin: 0 auto;
	border-radius: 8px;
	border: 2px solid rgba($color: $success, $alpha: 0.6);
	color: $success-contrast;

	@media only screen and (max-width: 576px) {
		width: auto;
		margin: 2rem 2rem;
	}

	button {
		font-weight: 700;
		max-width: 200px;
		// color: #0e3e14 !important;
		// background: rgba($color: #FFF, $alpha: 0.6) !important;
		// border: 2px #0e3e14 solid;
	}
}
.container {
	width: 100%;
	padding: 15px 10px;
}
.container h2 {
	text-align: center;
	margin-bottom: 15px;
}
textarea {
	height: 20px;
	width: 100%;
	border: none;
	border-bottom: 2px solid #aaa;
	background-color: transparent;
	margin-bottom: 10px;
	resize: none;
	outline: none;
	transition: 0.5s;
	color: #fff;
	font-size: 1rem;
}
button {
	color: #fff;
	background-color: transparent;
}
.btn {
	display: none;
}
.container h2 {
	text-align: left;
}
