.profile-page {
    // min-height: 100vh;
    height: calc(100vh - 70px);
    padding: 3rem 6rem;
    background-color: $primary;
    // background-image: linear-gradient(315deg, #000000 0%, #7f8c8d 74%);
    animation: fadeInAnimation ease 3s animation-iteration-count 1;
    animation-fill-mode: forwards;

    @media only screen and (max-width: 768px) {
        padding: 3rem 1rem;
    }

    @keyframes fadeInAnimation {
        0% {
            opacity: 0;
        }

        100% {
            opacity: 1;
        }
    }

    .domains {
        display: flex;
        flex-wrap: wrap;

        .domain {
            background: rgba($color: $secondary, $alpha: 0.8);
            display: flex;
            flex-direction: column;
            padding: 2rem;
            border-radius: 0.8rem;
            margin: 2rem 0;
            margin-right: 3rem;
            transition: 0.2s ease-in-out all;

            &:hover {
                background: rgba($color: $secondary, $alpha: 1);

                transform: scale(1.05);
            }

            img {
                width: 5rem;
                margin-right: 1rem;
            }

            h3 {
                font-size: 2rem;
            }

        }
    }



    input {
        font-size: 4rem;
        background: inherit;
        color: #fefefe;
    }

    .detail {
        margin: .25rem 0;

        &--username {
            font-size: 1.5rem;
        }

        opacity: .7;
    }

    .tasks-profile {
        margin-left: auto;
        animation: fadeInAnimation ease 3s animation-iteration-count 1;
        animation-fill-mode: forwards;
        color: $success;
    }

    @keyframes fadeInAnimation {
        0% {
            opacity: 0;
        }

        100% {
            opacity: 1;
        }
    }
}