@mixin flex-center-center {
  display: flex;
  justify-content: center;
  align-items: center;
}

@mixin flex-col-center-center {
  @include flex-center-center;
  flex-direction: column;
}

@mixin flex-start-center {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}


@mixin flex-col-start-center {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-direction: column;
}


html {
  scroll-behavior: smooth;
}

:root {
  @include respond(tab-land) {
    font-size: 14px;
  }

  @include respond(tab-port) {
    font-size: 12px;
  }

  @include respond(large-phone) {
    font-size: 10px;
  }

  @include respond(big-desktop) {
    font-size: 18px;
  }
}

*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

div,
main,
section,
aside {
  font-family: $font-family, sans-serif, serif;
}

body {
  background: linear-gradient(#141e30, #243b55);
  color: $text-color;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  overflow-x: hidden;
  min-height: 100vh;
}

a {

  &:link,
  &:visited {
    color: inherit;
    text-decoration: none;
  }
}

ul {
  list-style: none;
}

textarea {
  resize: none;
}

input,
textarea {
  border: none;

  &:focus {
    outline: none;
  }
}

button {
  border: none;
  cursor: pointer;

  &:focus {
    outline: none;
  }
}



.spinner {
  width: 64px;
  height: 64px;
  border: 8px solid;
  border-color: #3d5af1 transparent #3d5af1 transparent;
  border-radius: 50%;
  animation: spin 1.2s linear infinite;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}


.center-flex {
  @include flex-center-center;
}

.center-flex-col {
  @include flex-col-center-center;
}

.start-flex {
  @include flex-start-center;
}

.start-flex-col {
  @include flex-col-start-center;
}

.bg-contrast {
  background-color: rgba($color: $primary, $alpha: 0.9);
  box-shadow: 1px 1px 4px rgba($color: $primary, $alpha: 0.2);
  // border: 1px solid #FFF;
  padding: 1rem 0.6rem;
  border-radius: 1rem;
}