.tasks {
	padding: 2rem;
	min-height: 90vh;

	@media only screen and (max-width: 576px) {
		& {
			padding: 1rem 0;
		}
	}

	h1 {
		font-size: 2.5rem;
		margin-bottom: 1rem;
	}

	.cards {
		display: flex;
		flex-direction: column;
		align-items: center;
	}

	.task-card-single {
		position: relative;
		background-color: $secondary;
		padding: 2rem;
		border-radius: 25px;
		width: 100%;
		height: 100%;
		margin: 1rem auto;
		justify-self: center;
		max-width: 1015px;
		/*border: 2px solid white;*/

		@media only screen and (max-width: 576px) {
			& {
				width: auto;
			}
		}

		.pdf-container {
			// width: 50%;
			display: flex;
			align-items: center;

			img {
				width: 16px;
			}

			span {
				margin: 0 1rem;
			}
		}

		.comment-box {
			width: 100%;
			padding: 0.5rem;
			background: $primary;
		}

		.bottom {
			flex-direction: column;

			&:hover .img {
				text-decoration: underline;
			}

			.img {
				display: flex;
				flex-direction: column;
				align-items: center;
				font-size: 0.7rem;
				transition: 0.2s all;
			}

			p {
				width: 80%;
			}

			.iframe-container {
				display: flex;
				flex-direction: column;
			}

			iframe {
				margin: 1rem;
				margin-left: 0;
				min-width: 200px;
				min-height: 300px;
				width: 100%;
				height: 100%;
			}

			img {
				width: 3.5rem;
				margin-bottom: 0.2rem;
			}
		}

		.completed {
			font-size: 1.5rem;
			color: $success;
			position: absolute;
			top: 0;
			display: flex;
			text-align: center;
			vertical-align: middle;
			right: 0;
			transform: translate(25%, -25%);
			background-color: #fff;
			border-radius: 100%;
			width: 24px;
			height: 24px;
			transform: scale(1.5);
		}

		.date {
			opacity: 0.6;
		}
	}

	p {
		opacity: 0.8;
		margin-top: 1rem;
	}

	button {
		width: 100%;
		display: inline-block;
		margin-top: 1.5rem;
		padding: 0.6rem;
		border-radius: 10px;
		background-color: $primary;
		color: $text-color;
		text-align: center;
		font-size: 1rem;
		border: 1px solid #221232;
	}

	button:hover {
		background: #ffffff;
		color: #232b38;
		border: 1px solid #232b38;
		transition: all 0.2s ease-in-out;
		-webkit-transition: all 0.2s ease-in-out;
		-moz-transition: all 0.2s ease-in-out;
		-ms-transition: all 0.2s ease-in-out;
		-o-transition: all 0.2s ease-in-out;
	}
	button:disabled {
		background-color: #242527;
	}
	input[type="text"],
	select {
		width: 100%;
		padding: 12px 20px;
		margin: 8px 0;
		display: inline-block;
		border: 1px solid;
		border-radius: 4px;
		box-sizing: border-box;
	}
}

table {
	font-family: arial, sans-serif;
	border-collapse: collapse;
	width: 100%;
}

td,
th {
	border: 1px solid #dddddd;
	text-align: left;
	padding: 8px;
}

tr:nth-child(even) {
	background-color: #dddddd;
}
